.footer {
  display: flex;
  position: relative;
  width: 100%;
  flex-direction: column;
  align-items: center;
  gap: 60px;
}

.footer__socials {
  display: flex;
  flex-direction: row;
  gap: 40px;
}

.footer__socials-link {
  text-decoration: none;
  outline: none;
  position: relative;
}

.footer__rights {
  color: #bdbdbd;
  font-size: 14px;
  font-weight: 400;
  margin-bottom: 30px;
}

.arrow {
  position: absolute;
  display: inline-block;
  align-self: flex-end;
  top: 0;
}

.footer__socials-link::after {
  content: "";
  position: absolute;
  bottom: -3px;
  left: 0;
  width: 0;
  height: 2px;
  background-color: #0db2b3;
  transition: width 0.3s;
}

.footer__socials-link:hover::after {
  width: 100%;
}
